import React, {useEffect} from 'react';
import {BonusesCardsBlock, BonusesContent, BonusesWrapper, Text, TextBlock} from "./style";
import {JobHeader} from "../jobs/jobHeader";
import {ChoiceBonuse} from "./choiceBonuse";
import Clock from './assets/clock.svg';
import Palit from './assets/palit.svg';
import Hosting from './assets/hosting.svg';
import Sale from './assets/sale.svg';

import {BonusesCard} from "./bonusesCard";
import {Container} from "../../common/style/Container";
import {SliderBonuse} from "./sliderBonuse";
import {TitleHead} from "../services/benefit/style";
import {animateText} from "../../common/animate/animateText";

export type SaleCardsType = {
    id: string
    img: string,
    title: string,
    subtitle: string,
    text: string,
    subText: string
}
export const Bonuses = () => {

    useEffect(() => {
        document.title = '  VEON-TECH'
        animateText()
    }, [])

    const saleCards: SaleCardsType[] = [
        {
            id: '1',
            img: Clock,
            title: ' 100 BYN',
            subtitle: ' ',
            text: '     3     ',
            subText: '*   1500 BYN'
        },
        {
            id: '2',
            img: Palit,
            title: '',
            subtitle: '',
            text: '     3     ',
            subText: ''
        },
        {
            id: '3',
            img: Hosting,
            title: 'Hostfly.by',
            subtitle: '14  ',
            text: '*   ',
            subText: ''
        },
        {
            id: '4',
            img: Sale,
            title: '5% ',
            subtitle: '  ',
            text: '*     .   .   .',
            subText: ''
        },

    ]

    return (
        <BonusesWrapper>
            {/*<JobHeader title={''}*/}
            {/*           breadcrumbsTitle={''}*/}
            {/*/>*/}
            <Container>
                <TitleHead className='animate'></TitleHead>
            </Container>
            <ChoiceBonuse/>
            <Container>
                <BonusesCardsBlock>
                    {
                        saleCards.map((el, index) => (
                            <BonusesCard card={el} key={index}/>
                        ))
                    }
                </BonusesCardsBlock>
                <SliderBonuse saleCards={saleCards}/>
            </Container>
            <BonusesContent>
                <Container>
                    <TextBlock>
                        <Text className='animate'>  VEON-TECH     .  
                               ,
                                  ,  
                            
                                 .     
                            
                                 -    
                            
                             !
                        </Text>
                        <Text className='animate'>        
                            ,  
                                .
                        </Text>
                    </TextBlock>
                </Container>
            </BonusesContent>
        </BonusesWrapper>
    );
};

