import React from 'react';
import {
    About,
    AboutBlock,
    AboutQuality,
    BarLinks, BarLinksMobile,
    Card,
    CardsBlock,
    ContainerJob,
    DescriptionBar,
    DescriptionBlock,
    DescriptionContent,
    EchoBlock,
    EchoText,
    EchoTextBlock,
    EchoTitle,
    JobDescriptionWrapper,
    SubTitle,
    Title
} from "./style";
import {Link} from "react-router-dom";
import {Blank} from "../assets";
import {Accordeon} from "./accordeon";
import {TitleHead} from "../../services/benefit/style";
import {ROUTS} from "../../../common/constans/routs";

export const JobDescription = () => {
    const cardArrayData = [
        {title: '    '},
        {title: ',  '},
        {title: '   '},
        {title: '  '},
    ]
    const linksArrayData = [
        {title: '  ', to: ROUTS.ABOUT},
        {title: '  Veon-Tech', to: ROUTS.JOBS},
        {title: ' ', to: ''},
        {title: '', to: ROUTS.BONUSES},
    ]

    return (
        <JobDescriptionWrapper>
            <ContainerJob>
                <TitleHead className='animate'>
                      Veon-Tech</TitleHead>
                <DescriptionBlock>
                    <DescriptionContent>
                        <BarLinksMobile>
                            {linksArrayData.map((el, index) => <Link className='animate' key={index} to={el.to}>{el.title}</Link>)}
                        </BarLinksMobile>
                        <AboutBlock>
                            <About className='animate'>
                                    ,    
                                   -  -. <br/>    
                                  ,        :
                                clients@veon-tech.by
                            </About>
                            <About className='animate'>
                                        ,    
                                 ,     !
                            </About>
                        </AboutBlock>
                        <AboutQuality>
                            <SubTitle className='animate'>  ,    7-  !</SubTitle>
                            <Title>     Veon-Tech?</Title>
                            <CardsBlock>
                                {cardArrayData.map((el, index) => <Card className='animate' key={index}>{el.title}</Card>)}
                            </CardsBlock>
                        </AboutQuality>

                        <Accordeon/>

                    </DescriptionContent>
                    <DescriptionBar>
                        <BarLinks>
                            {linksArrayData.map((el, index) => <Link className='animate' key={index} to={el.to}>{el.title}</Link>)}
                        </BarLinks>
                        <EchoBlock>
                            <EchoTitle className='animate'></EchoTitle>
                            <EchoText>
                                      e-mail  .   
                                      3-5  .
                                <a rel="noreferrer" target={"_blank"} href={`mailto:clients@veon-tech.by`}>clients@veon-tech.by</a>
                            </EchoText>
                        </EchoBlock>
                        <EchoBlock>
                            <EchoTitle className='animate'>  ? !</EchoTitle>
                            <EchoTextBlock>
                                <Blank/>
                                <span>X, ! <br/>  </span>
                            </EchoTextBlock>
                        </EchoBlock>
                    </DescriptionBar>
                </DescriptionBlock>
            </ContainerJob>
        </JobDescriptionWrapper>
    );
};

