import React from 'react';
import {
    BenefitCard,
    BenefitContentBottom,
    BenefitContentTop,
    BenefitWrapper,
    SubTitle,
    Title,
    CardTitle,
    CardText, ircleWrap, ircle,

} from './style';
import {Container} from "../../../common/style/Container";

export const Benefit = () => {
    return (
        <BenefitWrapper>
            <Container>
                <BenefitContentTop>
                    <Title className='animate'>    <br/>   </Title>
                    <SubTitle> </SubTitle>
                </BenefitContentTop>
                <BenefitContentBottom>
                    <BenefitCard className='animate'>
                        <CardTitle>  </CardTitle>
                        <ircleWrap>
                            <ircle>
                                <span>01</span>
                            </ircle>
                        </ircleWrap>
                        <CardText>      .     .</CardText>
                    </BenefitCard>
                    <BenefitCard className='animate'>
                        <CardTitle>  </CardTitle>
                        <ircleWrap>
                            <ircle>
                                <span>02</span>
                            </ircle>
                        </ircleWrap>
                        <CardText>     ,      .</CardText>
                    </BenefitCard >
                    <BenefitCard className='animate'>
                        <CardTitle>  </CardTitle>
                        <ircleWrap>
                            <ircle>
                                <span>03</span>
                            </ircle>
                        </ircleWrap>
                        <CardText>             .</CardText>
                    </BenefitCard>
                </BenefitContentBottom>
            </Container>
        </BenefitWrapper>
    );
};

