import React from 'react';
import {
    BlockAbout,
    ElephantAboutContent,
    HappyElephantAboutWrapper,
    Title,
    Text,
    ColumnBlock,
    BlockMobile,
    TitleMob,
    TextMob,
    ColumnBlockMob,
    Img
} from './style';
import ElephfImage from '../assets/elephantAbutImage.webp'
import {Container} from "../../../common/style/Container";


export const HappyElephantAbout = () => {
    return (
        <HappyElephantAboutWrapper src={ElephfImage}>
            <Container>
                <BlockMobile>
                    <TitleMob>  </TitleMob>
                    <TextMob>C     ( )</TextMob>
                    <ColumnBlockMob>
                        <TextMob>
                              " "        
                             .       ,    
                             ,         .
                        </TextMob>
                        <TextMob>
                               ,    ,  
                            ,   ,    .
                        </TextMob>
                    </ColumnBlockMob>
                </BlockMobile>
            </Container>
            <Img src={ElephfImage}/>
            <Container>
                <ElephantAboutContent>
                    <BlockAbout>
                        <Title>  </Title>
                        <Text>C     ( )</Text>
                    </BlockAbout>
                    <ColumnBlock>
                        <Text>
                              " "        
                             .       ,    
                             ,         .
                        </Text>
                        <Text>
                               ,    ,  
                            ,   ,    .
                        </Text>
                    </ColumnBlock>
                </ElephantAboutContent>
            </Container>
        </HappyElephantAboutWrapper>
    );
};

