import React from 'react';
import {Block1, Block2, Block3, Block4, ContentBlock, ContentWrapper, Text, Title, TitleHeader} from './style';
import {Container} from '../../../common/style/Container';
import LockIcon from '@mui/icons-material/Lock';


export const Content = () => {

    const arrayData = [
        {
            title: '   ',
            text: '           '
        },
        {
            title: '  ,   ',
            text: '   ,   ,  ,      '
        },
        {
            title: '     ',
            text: '             '
        },
        {
            title: '      ',
            text: '           ',
        },
    ]


    return (
        <ContentWrapper>
            <Container>
                <TitleHeader className='animate'>   </TitleHeader>
                <ContentBlock>
                    <div className="parent">
                        <div className="div1 animate">
                            <Title>   </Title>
                            <Text>           </Text>
                        </div>
                        <div className="div2 animate">
                            <Title>  ,   </Title>
                            <Text>   ,   ,  , 
                                    </Text>
                        </div>
                        <div className="div3 animate">
                            <Title>     </Title>
                            <Text>         
                                   </Text>

                        </div>
                        <div className="div4 animate">
                            <Title>      </Title>
                            <Text>          
                                </Text>
                        </div>
                        <div className="div5"><LockIcon className={'LockIcon'}/></div>
                    </div>
                </ContentBlock>
            </Container>
        </ContentWrapper>
    );
};

